* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

.search-form {
  height: 50px;
  background-color: orange;
  justify-content: center;
  margin-bottom: 16px;
  display: flex;
}

.search-form input, button {
  height: 30px;
  align-self: center;
}

.search-form input:focus {
  border: none;
  outline-color: orange;
}

.search-form button {
  color: orange;
  background-color: #375ed5;
  border: none;
  font-weight: bold;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form button:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.load-more {
  height: 60px;
  width: 300px;
  color: orange;
  background-color: #375ed5;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  margin-left: 100px;
  font-size: 20px;
  font-weight: bold;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.load-more:hover {
  cursor: pointer;
  transform: scale(1.2);
}

